import React, { useState, useEffect, useCallback } from 'react';
import { 
  Box, 
  Typography, 
  Button, 
  Paper,
  IconButton,
  CircularProgress,
  useTheme
} from '@mui/material';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import PauseIcon from '@mui/icons-material/Pause';
import StopIcon from '@mui/icons-material/Stop';
import VoiceInput from './VoiceInput';

interface TimerProps {
  onTranscript?: (text: string) => void;
}

const Timer: React.FC<TimerProps> = ({ onTranscript }) => {
  const [time, setTime] = useState<number>(0);
  const [isRunning, setIsRunning] = useState<boolean>(false);
  const [targetTime, setTargetTime] = useState<number | null>(null);
  const theme = useTheme();

  useEffect(() => {
    let interval: NodeJS.Timeout;
    if (isRunning) {
      interval = setInterval(() => {
        setTime((prevTime) => {
          if (targetTime !== null && prevTime >= targetTime) {
            setIsRunning(false);
            return targetTime;
          }
          return prevTime + 1;
        });
      }, 1000);
    }
    return () => clearInterval(interval);
  }, [isRunning, targetTime]);

  const handleStart = () => {
    setIsRunning(true);
  };

  const handlePause = () => {
    setIsRunning(false);
  };

  const handleStop = () => {
    setIsRunning(false);
    setTime(0);
    setTargetTime(null);
  };

  const handleVoiceCommand = (text: string) => {
    const lowerText = text.toLowerCase();
    
    // Parse time from voice command
    const minutes = lowerText.match(/(\d+)\s*minute/i);
    const seconds = lowerText.match(/(\d+)\s*second/i);
    
    let totalSeconds = 0;
    if (minutes) totalSeconds += parseInt(minutes[1]) * 60;
    if (seconds) totalSeconds += parseInt(seconds[1]);
    
    if (totalSeconds > 0) {
      setTargetTime(totalSeconds);
      setTime(0);
      setIsRunning(true);
    }
    
    // Handle commands
    if (lowerText.includes('start') || lowerText.includes('begin')) {
      handleStart();
    } else if (lowerText.includes('pause') || lowerText.includes('stop')) {
      handlePause();
    } else if (lowerText.includes('reset')) {
      handleStop();
    }
  };

  const formatTime = (seconds: number): string => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  return (
    <Paper 
      elevation={3} 
      sx={{ 
        p: 3, 
        display: 'flex', 
        flexDirection: 'column', 
        alignItems: 'center',
        gap: 2
      }}
    >
      <Typography variant="h5" component="h2" gutterBottom>
        Voice Timer
      </Typography>
      
      <Box sx={{ position: 'relative', display: 'inline-flex' }}>
        <CircularProgress
          variant="determinate"
          value={targetTime ? (time / targetTime) * 100 : 0}
          size={120}
          thickness={4}
        />
        <Box
          sx={{
            top: 0,
            left: 0,
            bottom: 0,
            right: 0,
            position: 'absolute',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          <Typography variant="h4" component="div" color="text.secondary">
            {formatTime(time)}
          </Typography>
        </Box>
      </Box>

      <Box sx={{ display: 'flex', gap: 1 }}>
        <IconButton 
          onClick={handleStart} 
          color="primary"
          disabled={isRunning}
        >
          <PlayArrowIcon />
        </IconButton>
        <IconButton 
          onClick={handlePause} 
          color="primary"
          disabled={!isRunning}
        >
          <PauseIcon />
        </IconButton>
        <IconButton 
          onClick={handleStop} 
          color="primary"
        >
          <StopIcon />
        </IconButton>
      </Box>

      <VoiceInput onTranscript={handleVoiceCommand} toolId="timer" />
      
      <Typography variant="body2" color="text.secondary" align="center">
        Try saying: "Set timer for 2 minutes" or "Start timer"
      </Typography>
    </Paper>
  );
};

export default Timer; 