import React, { useState } from 'react';
import { 
  Box, 
  Typography, 
  Paper,
  IconButton
} from '@mui/material';
import ClearIcon from '@mui/icons-material/Clear';
import VoiceInput from './VoiceInput';

const Calculator: React.FC = () => {
  const [expression, setExpression] = useState<string>('');
  const [result, setResult] = useState<string>('');
  const [history, setHistory] = useState<Array<{expression: string, result: string, timestamp: Date}>>([]);

  const handleTranscript = (transcript: string) => {
    // Convert spoken words to mathematical expression
    let processedTranscript = transcript.toLowerCase()
      .replace(/plus/g, '+')
      .replace(/minus/g, '-')
      .replace(/times/g, '*')
      .replace(/multiplied by/g, '*')
      .replace(/divided by/g, '/')
      .replace(/equals/g, '=');

    setExpression(processedTranscript);

    try {
      // Evaluate the expression
      const calculatedResult = eval(processedTranscript.replace('=', ''));
      setResult(calculatedResult.toString());
      
      // Add to history
      setHistory(prev => [...prev, {
        expression: processedTranscript,
        result: calculatedResult.toString(),
        timestamp: new Date()
      }]);
    } catch (error) {
      setResult('Error');
    }
  };

  const clearCalculator = () => {
    setExpression('');
    setResult('');
  };

  return (
    <Paper elevation={3} sx={{ p: 3 }}>
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
        <Typography variant="h5" component="h2" sx={{ flexGrow: 1 }}>
          Voice Calculator
        </Typography>
        <IconButton onClick={clearCalculator} color="primary">
          <ClearIcon />
        </IconButton>
      </Box>

      <Box sx={{ mb: 3 }}>
        <Typography variant="h6" gutterBottom>
          Expression: {expression || 'No expression yet'}
        </Typography>
        <Typography variant="h4" color="primary">
          Result: {result || 'No result yet'}
        </Typography>
      </Box>

      <VoiceInput onTranscript={handleTranscript} toolId="calculator" />
      
      <Typography variant="body2" color="text.secondary" align="center" sx={{ mt: 1 }}>
        Click the microphone and speak your calculation (e.g., "five plus three")
      </Typography>
    </Paper>
  );
};

export default Calculator; 