import React, { useState, forwardRef, useImperativeHandle } from 'react';
import { 
  Box, 
  Typography, 
  Paper,
  TextField,
  MenuItem,
  IconButton,
  CircularProgress
} from '@mui/material';
import ClearIcon from '@mui/icons-material/Clear';
import VoiceInput from './VoiceInput';

const languages = [
  { code: 'en', name: 'English' },
  { code: 'es', name: 'Spanish' },
  { code: 'fr', name: 'French' },
  { code: 'de', name: 'German' },
  { code: 'it', name: 'Italian' },
  { code: 'pt', name: 'Portuguese' },
  { code: 'ru', name: 'Russian' },
  { code: 'ja', name: 'Japanese' },
  { code: 'ko', name: 'Korean' },
  { code: 'zh', name: 'Chinese' }
];

interface LanguageTranslatorProps {
  onTranscript?: (transcript: string) => void;
}

const LanguageTranslator = forwardRef<any, LanguageTranslatorProps>((props, ref) => {
  const { onTranscript } = props;
  const [sourceText, setSourceText] = useState<string>('');
  const [translatedText, setTranslatedText] = useState<string>('');
  const [sourceLang, setSourceLang] = useState<string>('en');
  const [targetLang, setTargetLang] = useState<string>('es');
  const [isTranslating, setIsTranslating] = useState<boolean>(false);

  // Expose methods to parent component via ref
  useImperativeHandle(ref, () => ({
    clearTranslator: () => {
      setSourceText('');
      setTranslatedText('');
    }
  }));

  const handleTranslate = async (text: string, from: string, to: string) => {
    setIsTranslating(true);
    try {
      // Here you would typically call a translation API
      // For now, we'll just simulate a delay
      await new Promise(resolve => setTimeout(resolve, 1000));
      setTranslatedText(`[Translated: ${text}]`);
    } catch (error) {
      setTranslatedText('Translation error occurred');
    } finally {
      setIsTranslating(false);
    }
  };

  const handleSourceTextChange = (text: string) => {
    setSourceText(text);
    if (text) {
      handleTranslate(text, sourceLang, targetLang);
    } else {
      setTranslatedText('');
    }
  };

  const handleSourceLangChange = (lang: string) => {
    setSourceLang(lang);
    if (sourceText) {
      handleTranslate(sourceText, lang, targetLang);
    }
  };

  const handleTargetLangChange = (lang: string) => {
    setTargetLang(lang);
    if (sourceText) {
      handleTranslate(sourceText, sourceLang, lang);
    }
  };

  const handleVoiceInput = (transcript: string) => {
    setSourceText(transcript);
    handleTranslate(transcript, sourceLang, targetLang);
    
    // Call the onTranscript prop if provided
    if (onTranscript) {
      onTranscript(transcript);
    }
  };

  const clearTranslator = () => {
    setSourceText('');
    setTranslatedText('');
  };

  return (
    <Paper elevation={3} sx={{ p: 3 }}>
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
        <Typography variant="h5" component="h2" sx={{ flexGrow: 1 }}>
          Language Translator
        </Typography>
        <IconButton onClick={clearTranslator} color="primary">
          <ClearIcon />
        </IconButton>
      </Box>

      <Box sx={{ mb: 3 }}>
        <TextField
          fullWidth
          select
          label="From Language"
          value={sourceLang}
          onChange={(e) => handleSourceLangChange(e.target.value)}
          sx={{ mb: 2 }}
        >
          {languages.map((lang) => (
            <MenuItem key={lang.code} value={lang.code}>
              {lang.name}
            </MenuItem>
          ))}
        </TextField>

        <TextField
          fullWidth
          multiline
          rows={3}
          value={sourceText}
          onChange={(e) => handleSourceTextChange(e.target.value)}
          placeholder="Enter text to translate..."
          sx={{ mb: 2 }}
        />
      </Box>

      <Box sx={{ mb: 3 }}>
        <TextField
          fullWidth
          select
          label="To Language"
          value={targetLang}
          onChange={(e) => handleTargetLangChange(e.target.value)}
          sx={{ mb: 2 }}
        >
          {languages.map((lang) => (
            <MenuItem key={lang.code} value={lang.code}>
              {lang.name}
            </MenuItem>
          ))}
        </TextField>

        <TextField
          fullWidth
          multiline
          rows={3}
          value={translatedText}
          InputProps={{ readOnly: true }}
          placeholder="Translation will appear here..."
        />
      </Box>

      {isTranslating && (
        <Box sx={{ display: 'flex', justifyContent: 'center', mb: 2 }}>
          <CircularProgress />
        </Box>
      )}

      <VoiceInput onTranscript={handleVoiceInput} toolId="translator" />
      
      <Typography variant="body2" color="text.secondary" align="center" sx={{ mt: 1 }}>
        Click the microphone and speak to translate
      </Typography>
    </Paper>
  );
});

export default LanguageTranslator; 