import React, { useState } from 'react';
import { 
  Box, 
  Typography, 
  Paper,
  TextField,
  MenuItem,
  Grid,
  IconButton
} from '@mui/material';
import ClearIcon from '@mui/icons-material/Clear';
import VoiceInput from './VoiceInput';

interface ConversionFactor {
  from: string;
  to: string;
  factor: number;
  offset?: number;
}

const conversionFactors: ConversionFactor[] = [
  // Length
  { from: 'meters', to: 'feet', factor: 3.28084 },
  { from: 'feet', to: 'meters', factor: 0.3048 },
  { from: 'kilometers', to: 'miles', factor: 0.621371 },
  { from: 'miles', to: 'kilometers', factor: 1.60934 },
  
  // Weight
  { from: 'kilograms', to: 'pounds', factor: 2.20462 },
  { from: 'pounds', to: 'kilograms', factor: 0.453592 },
  { from: 'grams', to: 'ounces', factor: 0.035274 },
  { from: 'ounces', to: 'grams', factor: 28.3495 },
  
  // Temperature
  { from: 'celsius', to: 'fahrenheit', factor: 1.8, offset: 32 },
  { from: 'fahrenheit', to: 'celsius', factor: 0.555556, offset: -32 }
];

const UnitConverter: React.FC = () => {
  const [fromUnit, setFromUnit] = useState<string>('meters');
  const [toUnit, setToUnit] = useState<string>('feet');
  const [fromValue, setFromValue] = useState<string>('');
  const [toValue, setToValue] = useState<string>('');

  const handleConversion = (value: string, from: string, to: string) => {
    const numValue = parseFloat(value);
    if (isNaN(numValue)) {
      setToValue('');
      return;
    }

    const conversion = conversionFactors.find(
      cf => cf.from === from && cf.to === to
    );

    if (conversion) {
      let result: number;
      if (from === 'celsius' && to === 'fahrenheit') {
        result = (numValue * conversion.factor) + (conversion.offset || 0);
      } else if (from === 'fahrenheit' && to === 'celsius') {
        result = (numValue + (conversion.offset || 0)) * conversion.factor;
      } else {
        result = numValue * conversion.factor;
      }
      setToValue(result.toFixed(2));
    } else {
      setToValue('');
    }
  };

  const handleFromValueChange = (value: string) => {
    setFromValue(value);
    handleConversion(value, fromUnit, toUnit);
  };

  const handleFromUnitChange = (unit: string) => {
    setFromUnit(unit);
    handleConversion(fromValue, unit, toUnit);
  };

  const handleToUnitChange = (unit: string) => {
    setToUnit(unit);
    handleConversion(fromValue, fromUnit, unit);
  };

  const handleVoiceCommand = (text: string) => {
    const lowerText = text.toLowerCase();
    
    // Extract number and units from voice command
    const numberMatch = lowerText.match(/(\d+(\.\d+)?)/);
    const fromUnitMatch = conversionFactors.find(cf => 
      lowerText.includes(cf.from)
    );
    const toUnitMatch = conversionFactors.find(cf => 
      lowerText.includes(cf.to)
    );

    if (numberMatch && fromUnitMatch && toUnitMatch) {
      setFromValue(numberMatch[1]);
      setFromUnit(fromUnitMatch.from);
      setToUnit(toUnitMatch.to);
      handleConversion(numberMatch[1], fromUnitMatch.from, toUnitMatch.to);
    }
  };

  const clearConverter = () => {
    setFromValue('');
    setToValue('');
  };

  return (
    <Paper elevation={3} sx={{ p: 3 }}>
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
        <Typography variant="h5" component="h2" sx={{ flexGrow: 1 }}>
          Unit Converter
        </Typography>
        <IconButton onClick={clearConverter} color="primary">
          <ClearIcon />
        </IconButton>
      </Box>

      <Grid container spacing={2}>
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            label="From"
            value={fromValue}
            onChange={(e) => handleFromValueChange(e.target.value)}
            type="number"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            label="To"
            value={toValue}
            InputProps={{ readOnly: true }}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            select
            label="From Unit"
            value={fromUnit}
            onChange={(e) => handleFromUnitChange(e.target.value)}
          >
            {Array.from(new Set(conversionFactors.map(cf => cf.from))).map((unit) => (
              <MenuItem key={unit} value={unit}>
                {unit.charAt(0).toUpperCase() + unit.slice(1)}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            select
            label="To Unit"
            value={toUnit}
            onChange={(e) => handleToUnitChange(e.target.value)}
          >
            {Array.from(new Set(conversionFactors.map(cf => cf.to))).map((unit) => (
              <MenuItem key={unit} value={unit}>
                {unit.charAt(0).toUpperCase() + unit.slice(1)}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
      </Grid>

      <Box sx={{ mt: 3 }}>
        <VoiceInput onTranscript={handleVoiceCommand} toolId="converter" />
        <Typography variant="body2" color="text.secondary" align="center" sx={{ mt: 1 }}>
          Try saying: "Convert 5 meters to feet"
        </Typography>
      </Box>
    </Paper>
  );
};

export default UnitConverter; 